import { Modal, message } from 'ant-design-vue';
import BfdUtil from '@baifendian/adherev-util';
import Intl from '@baifendian/adherev-util-intl';
import { openWarnDialog, openWarnMessage } from './warnprompt';
var withVue = BfdUtil._util.withVue;
var WarnPrompt = {
    openWarnMessage: openWarnMessage,
    openWarnDialog: openWarnDialog,
    isUse: function () { return true; },
    use: function (Vue) {
        Intl.isUse() && Intl.use(Vue);
        Vue.use(message);
        Vue.use(Modal);
        withVue(Vue, 'WarnPrompt', WarnPrompt);
    },
};
export default WarnPrompt;
